<template>
  <div class="goodsList">
    <div class="top">
      <van-icon
        name="arrow-left"
        size="22"
        color="#ff8600"
        @click="onClickLeft"
      />
      <van-search
        v-model="value"
        shape="round"
        placeholder="请输入搜索关键词"
        @search="onSearch"
      >
      </van-search>
      <van-button
        size="small"
        round
        color="linear-gradient(to right,#ff9000 0,#ff5000 100%)"
        @click="onSearch"
        >搜索</van-button
      >
    </div>

    <!-- 导航栏 -->
    <ul class="tabbar">
      <li
        v-for="(item, i) in navList"
        :key="i"
        :class="{ active: active == i }"
        @click="onChange(i)"
      >
        {{ item }}
      </li>
    </ul>

    <div class="content">
      <GoodsList :List="goodsData"></GoodsList>
      <div class="empty" v-if="goodsData == 0">
        <div class="img">
          <img src="../assets/image/EmptyData.png" />
          <p>暂无商品，去看看别的吧</p>
        </div>
        <GoodsList :List="LikeData"
          ><div class="list_hd">
            <img
              src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png"
            /></div
        ></GoodsList>
      </div>
    </div>
  </div>
</template>

<script>
import GoodsList from "../components/GoodsList";
export default {
  name: "goodsList",
  data() {
    return {
      id: "",
      // 当前输入的值
      value: "",

      active: 0,

      navList: ["全部", "价格", "销量", "新品"],

      isNews: 0,

      isPriceOrder: "",

      isSalesOrder: "",

      isToggle: true,

      url: "",

      // 商品数据
      goodsData: [],

      //猜你喜欢
      LikeData: [],

      show: false,
    };
  },

  components: {
    GoodsList,
  },

  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (from.fullPath == "/search") {
        vm.value = vm.$route.params.val;
        if (vm.value) {
          vm.onSearch();
        }
      } else if (from.fullPath == "/category") {
        vm.id = vm.$route.params.val;
        vm.url = `products?page=1&limit=8&keyword=&sid=${vm.id}&news=0&priceOrder=&salesOrder=`;
        vm.getGoodsData();
      }
    });
  },

  created() {
    // if (typeof(this.$route.params.val) == ) {
      
    // }
    // console.log(typeof(this.$route.params.val));
    // this.id = this.$route.params.val;
    // this.url = `products?page=1&limit=8&keyword=&sid=${this.id}&news=0&priceOrder=&salesOrder=`;
    // this.getGoodsData();

    // 获取猜你喜欢数据
    this.axios(this.baseUrl + "/groom/list/2?page=1&limit=30").then((data) => {
      this.LikeData = data.data.data.list;
    });
  },
  methods: {
    //返回上一级
    onClickLeft() {
      this.$router.back();
    },

    // 搜索商品
    onSearch() {
      if (!this.value) {
        this.$toast({
          message: "请输入搜索内容",
          forbidClick: true,
          duration: 3000,
        });
        return;
      }

      this.axios({
        method: "GET",
        url: this.baseUrl + "/products?page=1&limit=20&keyword=" + this.value,
      }).then((res) => {
        if (res.data.status == 200) {
          this.goodsData = res.data.data;
        }
      });

      this.show = true;
    },

    //切换商品类型
    onChange(index) {
      this.isNews = 0;
      this.isPriceOrder = "";
      this.isSalesOrder = "";
      this.active = index;

      if (index == 1) {
        if (this.isToggle) {
          this.isPriceOrder = "asc";
        } else {
          this.isPriceOrder = "desc";
        }
        this.isToggle = !this.isToggle;
      } else if (index == 2) {
        if (this.isToggle) {
          this.isSalesOrder = "asc";
        } else {
          this.isSalesOrder = "desc";
        }
        this.isToggle = !this.isToggle;
      } else if (index == 3) {
        this.isNews = 1;
        this.isSalesOrder = "desc";
      }

      this.url = `/products?page=1&limit=8&keyword=&sid=${this.id}&news=${this.isNews}&priceOrder=${this.isPriceOrder}&salesOrder=${this.isSalesOrder}`;

      this.getGoodsData();
    },

    //获取商品数据
    getGoodsData() {
      this.axios({
        method: "GET",
        url: this.baseUrl + `/${this.url}`,
      })
        .then((res) => {
          if (res.data.status == 200) {
            this.goodsData = res.data.data;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },
  },
};
</script>

<style lang="less" scoped>
.goodsList {
  .top {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 0 10px;
    display: flex;
    align-items: center;
    background: #fff;
    .van-search {
      flex: 1;
      background: none;
      .van-search__content {
        border: 1px solid #ff8500;
      }
    }
    .van-button__text {
      font-size: 16px;
    }
  }
  .tabbar {
    width: 100%;
    position: fixed;
    top: 56px;
    display: flex;
    text-align: center;
    padding: 10px 0;
    background: #fff;
    li {
      flex: 1;
      font-size: 16px;
    }
  }
  .active {
    color: #ff8500;
  }
  .content {
    padding: 110px 0 0;
  }
  .img {
    margin-bottom: 20px;
    text-align: center;
    p {
      color: #666;
    }
  }
}
</style>